.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__image {
  text-align: center;
  margin: 16px 0 36px;
}

.card__photo {
  height: 134px;
}

.card__name {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
}

.card__reviews {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.card__grade {
  line-height: 16px;
}

.card__prices {
  justify-content: space-between;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.card__text {
  color: #616070;
  font-size: 12px;
}

.card__price {
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  box-sizing: border-box;
  text-align: center;
  cursor: pointer;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  transition-duration: .3s;
}

.card:hover {
  transition: transform .3s ease-in-out;
  transform: scale(1.2);
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
  position: relative;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav {
  text-align: center;
}

.nav__list {
  display: flex;
}

.nav__item {
  list-style-type: none;
  display: inline-block;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  margin-left: 0;
  position: relative;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.page {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-style: normal;
}

.page__body {
  margin: 0;
}

.container {
  --colum-count: 1;
  grid-template-columns: repeat(var(--colum-count), 200px);
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    --colum-count: 2;
  }
}

@media (width >= 768px) {
  .container {
    --colum-count: 3;
  }
}

@media (width >= 1024px) {
  .container {
    --colum-count: 4;
  }
}

ul, li, a, p {
  margin: 0;
  padding: 0;
}
